* {
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
  }
  body {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: #95a5a6;
  }
  
  .container {
    width: 500px;
    height: 465px;
    background: #fff;
    box-shadow: 8px 8px 11px;
    position: relative;
  }

  .wrapper {
    position: absolute;
    height: 27rem;
    width: 100%;
    margin-top:  27px;
  }

  .top_head{
    height: 5px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .top_head>div{
    margin-right: 3em;
    margin-top: 26px;
  }

  .top_head >span{
    margin-top: 20px;
    margin-left: 42px;
  }
  
  .post_area {
      resize: none;
      position: absolute;
      height: 11rem;
      width: 25rem;
      margin-left: 5rem;
      margin-top: 0.2rem;
      outline: none;
    }

    .post_area[contentEditable="true"]:empty:not(:focus):before {
      content: attr(placeholder)
    }
  .emoj {
    margin-left: 26rem;
  }
  
  .profile {
    position: absolute;
    font-size: 71px !important;
  }
  
  .post_area {
    border: 1px solid #0984e3;
    height: 11rem;
    width: 25rem;
    margin-left: 5rem;
  }
  
  
  
  .content {
    height: 14rem;
    width: 100%;
    position: relative;
  }
  
  .theme-emoji {
    position: absolute;
    margin-top: 12rem;
    margin-left: 3rem;
  }
  
  .opt {
    display: flex;
  }
  .tgs,
  .loc,
  .gif,
  .event {
      cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 5px;
    width: 10rem;
    height: 2rem;
    margin-top: 2rem;
    background-color: #ecf0f1;
  }
  .loyalty,
  .location,
  .gif_box,
  .event_note {
    cursor: pointer;
  }
  
  .list_1 {
    margin-left: 13.5px;
  }
  
  .list_2 {
    margin-left: 9rem;
  }
  
  .btns {
    margin-top: 3rem;
    margin-left: 20rem;
    display: flex;
  }
  
  .lock {
    font-size: 15px !important;
  }
  
  .btn_post_only {
    display: flex;
    justify-content: center;
    margin-right: 10px;
    border: none;
    border-radius: 5px;
  }
  
  .btn_post {
    background-color: #54a0ff;
    border: none;
    border-radius: 5px;
    color: white;
  }
  
  .gify_container{
    height: 15rem;
    width: 10.8rem;
    position: absolute;
    margin-top: 24rem;
    margin-left: 0.8rem;
    background-color: white;
    overflow-y: auto;
  }

 
  img{
    height:50%;
    width: 100%;
    margin-top: 2px;
    object-fit:contain;
  }

  input{
    outline: none;
  }

  